import {
  createUserWithEmailAndPassword,
  getAuth,
  signOut
} from '@firebase/auth';
import { doc, getFirestore, setDoc } from 'firebase/firestore';
import { Field, Form, Formik } from 'formik';
import React, { FC, useState } from 'react';
import { Link } from 'react-router-dom';
import Loader from '../components/UI/loader/Loader';

const SignUpPage: FC = () => {
  const [loading, setLoading] = useState(false);
  const [currentUser, setCurrentUser] = useState({
    firstName: '',
    lastName: '',
    email: '',
    password: '',
  });

  const handleSubmit = async (values: any) => {
    if (values.password === values.rePassword) {
      setLoading(true);
      try {
        const db = getFirestore();
        const res = await createUserWithEmailAndPassword(
          getAuth(),
          values.email,
          values.password
        );
        await setDoc(doc(db, 'users', res.user.uid), {
          firstName: values.firstName,
          lastName: values.lastName,
          phoneNumber: '+38' + values.phoneNumber,
        });
        setCurrentUser({
          firstName: values.firstName,
          lastName: values.lastName,
          password: values.password,
          email: values.email,
        });
        await signOut(getAuth());
        setLoading(false);
      } catch (e) {
        console.log(e);
        setLoading(false);
      }
    }
  };
  return (
    <div className="new-user-page">
      <div className="back-bar">
        <Link to="/login" className="new-user-btn">
          Back to Login
        </Link>
      </div>
      <h3>Create new USER account:</h3>
      <Formik
        initialValues={{
          email: '',
          firstName: '',
          lastName: '',
          password: '',
          rePassword: '',
          phoneNumber: '',
        }}
        onSubmit={(values, { resetForm }) => {
          handleSubmit(values);
          resetForm();
        }}
      >
        <Form className="new-user-form">
          <label htmlFor="email">Email:</label>
          <Field
            id="email"
            name="email"
            placeholder="jane@acme.com"
            type="email"
          />
          <label htmlFor="firstName">First Name:</label>
          <Field id="firstName" name="firstName" placeholder="Jane" />

          <label htmlFor="lastName">Last Name:</label>
          <Field id="lastName" name="lastName" placeholder="Doe" />
          <label htmlFor="phoneNumber">Phone number:</label>
          <Field id="phoneNumber" name="phoneNumber" placeholder="0501231231" />
          <label htmlFor="password">Password:</label>
          <Field id="password" name="password" placeholder="some password" />
          <label htmlFor="rePassword">Re-type password:</label>
          <Field
            id="rePassword"
            name="rePassword"
            placeholder="re-type password"
          />

          <button type="submit">Create new User</button>
        </Form>
      </Formik>
      {loading && <Loader />}
      {currentUser.email && (
        <div>
          Last created user: {currentUser.firstName} {currentUser.lastName} with
          password: {currentUser.password}
        </div>
      )}
    </div>
  );
};

export default SignUpPage;
