import { initializeApp } from '@firebase/app';
import React, { FC, useEffect, useState } from 'react';
import AppRouter from './components/AppRouter';
import { AuthContext } from './context/AuthContext';

initializeApp({
  apiKey: 'AIzaSyBvRkqo5dzUAZ4J6Uo3BfWtg_-hRw4LPAs',
  authDomain: 'crc-expo-firestore.firebaseapp.com',
  projectId: 'crc-expo-firestore',
  storageBucket: 'crc-expo-firestore.appspot.com',
  messagingSenderId: '163188046866',
  appId: '1:163188046866:web:7184760fdab9d687b6accb',
});

const App: FC = () => {
  const [conIsAuth, conSetIsAuth] = useState(false);

  useEffect((): void => {
    if (localStorage.getItem('auth')) {
      conSetIsAuth(true);
    }
  }, []);


  return (
    <AuthContext.Provider
      value={{
        conIsAuth,
        conSetIsAuth,
      }}
    >
      <AppRouter />
    </AuthContext.Provider>
  );
};

export default App;
