import * as firebase from 'firebase/app';
import {
  collection,
  getDocs,
  getFirestore,
  query,
  where,
} from 'firebase/firestore';
import React, { FC, useState } from 'react';
import DefaultButton from './UI/button/DefaultButton';
import DefaultInput from './UI/input/DefaultInput';
import { useHistory } from 'react-router';

interface User {
  id: string;
  data: {};
}

interface IUser {
  id: string;
  data: {};
}

const SearchForm: FC = () => {
  const [inputValue, setInputValue] = useState('');
  const [search, setSearch] = useState<[]>([]);
  const db = getFirestore();
  const router = useHistory();

  async function getUser(name: string) {
    const q = query(collection(db, '/users'), where('firstName', '==', name));
    const snap = await getDocs(q);
    if (snap.empty) {
      console.log('Nothing to view');
    } else {
      const resultArray: any = [];
      snap.forEach((doc) => {
        resultArray.push({ id: doc.id, data: doc.data() });
      });
      setSearch(resultArray);
      console.log(firebase.getApps());
    }
  }

  return (
    <div>
      <DefaultInput
        placeholder="Search form"
        onChange={(e: React.ChangeEvent<HTMLInputElement>) =>
          setInputValue(e.target.value)
        }
      />
      <DefaultButton onClick={() => getUser(inputValue)}>Search</DefaultButton>
      {search.length > 0 ? (
        <div>Найдено: {search.length} записей</div>
      ) : (
        <div />
      )}
      {search.length > 0 ? (
        search.map((el: IUser) => (
          <div key={el.id} onClick={() => router.push(`/users/${el.id}`)}>
            {el.id}
          </div>
        ))
      ) : (
        <div>Nothing to view</div>
      )}
    </div>
  );
};

export default SearchForm;
