export interface Auto {
  engine: string;
  lastVisitToService: Date;
  manufacturer: string;
  mileage: number;
  model: string;
  modification: string;
  productionYear: number;
  regCity: string;
  regNumber: string;
  regSeries: string;
  registrationDate: Date;
  transmission: string;
  vin: string;
}

export enum AutoActionEnum {
  SET_AUTO = 'SET_AUTO',
  SET_LOADING = 'SET_LOADING',
  SET_ERROR = 'SET_ERROR',
}

export interface setAutoAction {
  type: AutoActionEnum.SET_AUTO;
  payload: Auto;
}

export interface setLoadingAction {
  type: AutoActionEnum.SET_LOADING;
  payload: boolean;
}

export interface setErrorAction {
  type: AutoActionEnum.SET_ERROR;
  payload: string;
}

export type AutoAction = setAutoAction | setLoadingAction | setErrorAction;
