import { Auto, AutoAction, AutoActionEnum } from './types';

const initialState = {
  auto: {} as Auto,
  isLoading: false,
  error: '',
};

export default function autoReducer(state = initialState, action: AutoAction) {
  switch (action.type) {
    case AutoActionEnum.SET_AUTO:
      return { ...state, auto: action.payload, isLoading: false };
    case AutoActionEnum.SET_ERROR:
      return { ...state, error: action.payload, isLoading: false };
    case AutoActionEnum.SET_LOADING:
      return { ...state, isLoading: action.payload };
    default:
      return state;
  }
}
